.gallery {
  $gap: 20px;

  &__caption {
    margin: 12px 0 35px;
  }

  &__pics-block {
    display: flex;
    justify-content: space-between;
  }

  &__photo {
    width: calc(50% - #{$gap / 2});
    height: 260px;
    object-fit: cover;
    object-position: center;
  }

  &_wide-left {
    .gallery__photo:first-child {
      width: calc(67% - #{$gap / 2});
    }

    .gallery__photo:last-child {
      width: calc(33% - #{$gap / 2});
    }
  }

  &_wide-right {
    .gallery__photo:first-child {
      width: calc(33% - #{$gap / 2});
    }

    .gallery__photo:last-child {
      width: calc(67% - #{$gap / 2});
    }
  }

  &__button {
    margin: 9px auto 0;
  }
}
